<template>
    <b-container fluid>
      <!-- User Interface controls -->
      <b-row>
        <b-col lg="6" class="my-1 d-flex">

          <!-- gender filter -->

          <b-form-group
            class="mb-0 mr-2"
          >
            <b-input-group size="sm">
              <b-form-input
                id="filter-input"
                v-model="filterName"
                type="search"
                placeholder="Name"
              ></b-form-input>
            </b-input-group>
          </b-form-group>

          <!-- gender filter -->

          <b-form-group
            class="mb-0 mr-2"
          >
            <b-input-group size="sm">
              <v-select style="width: 250px;" :options="['erkak','ayol']" v-model="filterGender" placeholder="Gender"></v-select>
            </b-input-group>
          </b-form-group>

          <!-- age filter -->

          <b-form-group
            class="mb-0 mr-2"
          >
            <b-input-group size="sm">
              <b-form-input
                id="filter-age"
                v-model="filterAge"
                type="search"
                placeholder="Age"
              ></b-form-input>
            </b-input-group>
          </b-form-group>

          <!-- address -->
          <b-form-group
            class="mb-0 mr-2"
          >
            <b-input-group size="sm">
              <b-form-input
                id="filter-address"
                v-model="filterAddress"
                type="search"
                placeholder="Address"
              ></b-form-input>
            </b-input-group>
          </b-form-group>
          <!-- comment -->
          <b-form-group
            class="mb-0 mr-2"
          >
            <b-input-group size="sm">
              <b-form-input
                id="filter-address"
                v-model="filterComment"
                type="search"
                placeholder="Comment"
              ></b-form-input>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col sm="5" md="6" class="my-1">
          <b-form-group
            :label="table.perPage[lang]"
            label-for="per-page-select"
            label-cols-sm="6"
            label-cols-md="4"
            label-cols-lg="3"
            label-align-sm="right"
            label-size="sm"
            class="mb-0"
          >
            <v-select style="width: 200px;" v-model="perPage" :options="pageOptions"></v-select>
          </b-form-group>
        </b-col>
  

      </b-row>
  
      <!-- Main table element -->
      <b-table
        :items="filteredTableData"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter-included-fields="filterOn"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        stacked="md"
        show-empty
        small
        @filtered="onFiltered"
        bordered
        striped
        class="mt-4"
      >
        <template #cell(name)="row">
          {{ row.value }}
        </template>

        <template #cell(actions)="row" class="form-actions">
          <b-button size="sm" variant="success" @click="editTodo(row.item.id)">
            edit
          </b-button>
          <b-button size="sm" variant="danger" @click="delTodo(row.item.id)">
            delete
          </b-button>
        </template>
  
        <template #row-details="row">
          <b-card>
            <ul>
              <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
            </ul>
          </b-card>
        </template>
      </b-table>
      
      <b-row>
        <b-col sm="7" md="6" class="my-1">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="fill"
            size="sm"
            class="my-0"
          ></b-pagination>
        </b-col>
      </b-row>
    </b-container>
  </template>
  
  <script>
import lang from '@/lang'

export default {
  props: {
    table:Object,
  },
  computed: {
    filteredTableData() {
      if (this.filterName) {
        const filterText = this.filterName.toLowerCase();
        return this.$store.state.todo.filter(item =>
          item.name.toLowerCase().includes(filterText)
        );
      }
      if (this.filterGender) {
        const filterText = this.filterGender.toLowerCase();
        return this.$store.state.todo.filter(item =>
          item.gender.toLowerCase().includes(filterText)
        );
      }
      if (this.filterAge) {
        const filterText = this.filterAge.toLowerCase();
        return this.$store.state.todo.filter(item =>
          item.age.toLowerCase().includes(filterText)
        );
      }
      if (this.filterAddress) {
        const filterText = this.filterAddress.toLowerCase();
        return this.$store.state.todo.filter(item =>
          item.address.toLowerCase().includes(filterText)
        );
      }
      if (this.filterComment) {
        const filterText = this.filterComment.toLowerCase();
        return this.$store.state.todo.filter(item =>
          item.comment.toLowerCase().includes(filterText)
        );
      }
      
      // if (condition) {
        
      // }
      return this.$store.state.todo;
    },
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key }
        })
    },
    lang(){
      return this.$store.state.lang
    },
    fields() {
      return [
      { key: 'name', label: this.table.name[this.lang], sortable: true, sortDirection: 'desc', variant: 'info'},
      { key: 'gender', label: this.table.gender[this.lang],variant: 'success' },
      { key: 'age', label: this.table.age[this.lang], sortable: true, class: 'text-center',  variant: 'primary' },
      { key: 'address', label: this.table.address[this.lang], variant: 'success' },
      { key: 'comment', label: this.table.comment[this.lang], variant: 'primary' },
      { key: 'actions', label: this.table.actions[this.lang], variant: 'info',sortable: false }
    ]
  }
  },
  data() {
    return {
      filterName: '',
      filterGender: '',
      filterAge: '',
      filterAddress: '',
      filterComment: '',
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: "Show a lot" }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: []
    }
  },
  mounted() {
    this.totalRows = this.$store.state.todo.length
  },
  updated() {
    this.totalRows = this.$store.state.todo.length
  },
  methods: {
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`
      this.infoModal.content = JSON.stringify(item, null, 2)
      this.$root.$emit('bv::show::modal', this.infoModal.id, button)
    },
    resetInfoModal() {
      this.infoModal.title = ''
      this.infoModal.content = ''
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
    editTodo(id){
        let obj = {
            bool: true,
            type: 'edit',
            id
        }
        this.$store.commit('addModal', obj)
    },
    delTodo(id){
        this.$store.commit('delTodo', id)
    }
  }
}
</script>