<template>
  <div id="app">
    <Filter />
    <div class="container crud">
      <div class="lang mt-4">
        <v-select v-model="lang" :options="['uz', 'ru', 'eng', 'krill', 'kazak']"></v-select>
      </div>
      <Navbar :navbarLang="langWord.navbar"/>
      <Table :table="langWord.table"/>
      
    </div>
    <transition name="science">
      <AddTable v-if="this.$store.state.modal.bool" :addTable="langWord.addTable"/>
    </transition>
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue';
import Table from './components/Table.vue';
import AddTable from './components/AddTable.vue';
import lang from "./lang";
import Filter from "./components/Filter.vue";

export default {
  name: 'App',
  components: {
    Navbar,
    Table,
    AddTable,
    Filter
  },
  computed: {
  },
  data() {
    return {
      count: 0,
      langWord: {},
      lang: this.$store.state.lang
    }
  },
  watch: {
    lang(val){
      if (val == 'uz' || val == 'ru' || val == 'eng' || val == 'krill' || val == 'kazak') {
        this.$store.commit('langMutat',val)
      }
    }
  },
  methods: {

  },
  created() {
    this.langWord = lang;
    this.$store.commit('init')
    this.$store.dispatch('getCart')
  },

}
</script>

<style lang="scss">
.lang{
  display: flex;
  flex-direction: column;
  max-width: 150px;
  gap: 10px;
  margin-left: auto;
}
</style>
