<template>
    <div class="addTable">
        <div class="addTable__card">
            <span class="addTable__card-close" @click="closeModal">
                <img src="../assets/img/close.svg" alt="">
            </span>

            <h5>{{ addTable.title[lang] }}</h5>
            <b-form class="card__item d-flex gap-4 flex-wrap">
                <b-form-input
                    v-model="name"
                    :placeholder="addTable.name[lang]"
                    required
                    style="width: 330px;"
                ></b-form-input>
                <b-form-input
                    v-model="age"
                    :placeholder="addTable.age[lang]"
                    type="number"
                    style="width: 100px;"
                ></b-form-input>
                <b-form-input
                    v-model="address"
                    :placeholder="addTable.address[lang]"
                    type="text"
                    style="width: 330px;"
                ></b-form-input>
                <div class="card__gender">
                    <label>{{ addTable.gender[lang] }}</label>
                    <v-select style="width: 200px;" v-model="gender" :options="[addTable.gender.male[lang], addTable.gender.female[lang]]"></v-select>
                </div>
                <div class="card__izoh">
                    <b-form-textarea
                        id="textarea"
                        v-model="comment"
                        :placeholder="addTable.comment[lang]"
                    ></b-form-textarea>
                </div>
            </b-form>
            <div class="science mt-4 mb-3 d-flex">
                <h5>{{ addTable.science.title[lang] }}</h5>
                <span v-b-modal.modal-lg class="navbar__info science__create" @click="scienceOpen">
                    <span>{{ addTable.science.addScience[lang] }}</span>
                    <img class="navbar__plus" src="../assets/img/plus.svg" alt="">
                </span>

                <b-modal v-if="name && age && address && gender && comment" id="modal-lg" size="lg" title="Large Modal" hide-footer hide-header>
                    <div class="science__parent mt-3">
                        <b-form class="science__item card__item d-flex gap-4 flex-wrap">
                            <b-form-input
                                :placeholder="addTable.science.scienceName[lang]"
                                required
                                style="width: 220px;"
                                v-model="science"
                            ></b-form-input>
                            <b-form-input
                                :placeholder="addTable.science.teacherName[lang]"
                                required
                                style="width: 220px;"
                                v-model="teacher"
                            ></b-form-input>
                            <b-form-input
                                :placeholder="addTable.science.ball[lang]"
                                required
                                type="number"
                                style="width: 220px;"
                                v-model="ball"
                            ></b-form-input>
                            <span class="card__item-date">
                                <label for="card__item-date">{{ addTable.science.data.dataOn[lang] }}</label>
                                <b-form-input
                                    id="card__item-date"
                                    required
                                    style="width: 220px;"
                                    type="time"
                                    v-model="dataOn"
                                ></b-form-input>
                            </span>
                            <span class="card__item-date">
                                <label for="card__item-date2">{{ addTable.science.data.dataOut[lang] }}</label>
                                <b-form-input
                                    id="card__item-date2"
                                    required
                                    style="width: 220px;"
                                    type="time"
                                    v-model="dataOut"
                                ></b-form-input>
                            </span>
                        </b-form>
                        <div class="science__parent-control">
                            <b-button variant="success" @click="scienceAdd">{{ editScienceInfo.type == 'add' ? "Add" : "Save" }}</b-button>
                        </div>
                    </div>
                </b-modal>
            </div>
            <ChildScience @editScienceVal="editScienceVal" :parentId="parentId()" :scienceLang="addTable.science"/>
            <div class="addTable__card-info">
                <b-button @click="cansel">Cansel</b-button>
                <b-button variant="success" @click="addTodo">{{ modal.type == 'add' ? "Add" : "Save" }}</b-button>
            </div>
        </div>
    </div>
</template>
<script>
import ChildScience from '../components/ChildScience.vue'

export default {
    components:{
        ChildScience
    },
    props: {
        addTable:Object
    },
    data() {
        return {
            name: "",
            age: "",
            address: "",
            gender: "",
            comment: "",
            modal: this.$store.state.modal,
            scienceDemo: this.$store.state.scienceDemo,
            // Science
            science: "",
            teacher: "",
            ball: "",
            dataOn: "",
            dataOut: "",
        }
    },
    methods: {
        parentId(){
            if (this.modal.type == 'edit') return this.modal.id
            else return this.$store.state.id
        },
        closeModal(){
            let obj = {
                bool: false,
                type: 'add'
            }
            this.$store.commit('addModal', obj)
            this.$store.commit('clearScienceDemo',this.parentId())
        },
        addTodo(variant = null){
            if (this.name && this.age && this.address && this.gender && this.comment) {
                if (this.$store.state.modal.type == 'add') {
                    let object = {
                        id: this.$store.state.id,
                        name: this.name,
                        age: this.age,
                        address: this.address,
                        gender: this.gender,
                        comment: this.comment
                    }

                    this.$store.commit('addTodo',object)
                    this.$store.commit('id')
                    this.clearTodo()
                    let obj = {
                        bool: false,
                        type: 'add'
                    }
                    this.$store.commit('addModal', obj)
                } else{
                    let cardId = this.$store.state.modal.id
                    let object = {
                        id: cardId,
                        name: this.name,
                        age: this.age,
                        address: this.address,
                        gender: this.gender,
                        comment: this.comment
                    }
                    this.$store.commit('editTodo', object)
                    this.clearTodo()
                }
                this.$store.commit('oldScienceDemo')
            }
            else{
                variant = 'danger'
                this.$bvToast.toast("Maydonlarning hammasi to'ldirilishi shart", {
                    title: `Variant ${variant || 'default'}`,
                    variant: variant,
                    solid: true,
                    autoHideDelay: 50000,
                })
            }
            // 
        },
        cansel(){
            this.$store.commit('clearScienceDemo',this.parentId())
            this.clearTodo()
        },
        clearTodo(){
            this.name = ""
            this.address = ""
            this.age = ""
            this.gender = ""
            this.comment = ""

            let obj = {
                bool: false,
                type: 'add'
            }
            this.$store.commit('addModal', obj)
        },
        scienceOpen(variant = null){
            if (this.name && this.age && this.address && this.gender && this.comment) {
                let obj = {
                    type: 'add'
                }
                this.$store.commit('addScienceInfo', obj)
                this.scienceClear()
            }else{
                variant = 'danger'
                this.$bvToast.toast("O'quvchining hamma malumoti kiritilmagan", {
                    title: `Variant ${variant || 'default'}`,
                    variant: variant,
                    solid: true,
                    autoHideDelay: 50000,
                })
            }
        },
        scienceAdd(variant = null){
            if (this.science && this.teacher && this.ball && this.dataOn && this.dataOut) {
                if (this.editScienceInfo.type == 'add') {
                    let obj = {
                        id: this.$store.state.scienceId,
                        newCreate: true,
                        newUpdate: false,
                        parentId: this.parentId(),
                        science: this.science,
                        teacher: this.teacher,
                        ball: this.ball,
                        data: { dataOn: this.dataOn, dataOut: this.dataOut}
                    }
                    this.$store.commit('addScienceDemo', obj)
                    this.$store.commit('scienceId')
                }else{
                    let obj = {
                        science: this.science,
                        teacher: this.teacher,
                        ball: this.ball,
                        data: { dataOn: this.dataOn, dataOut: this.dataOut}
                    }
                    this.$store.commit('newScienceDemo', obj)
                    let object = {
                        type: 'add'
                    }
                    this.$store.commit('addScienceInfo', object)
                }
                this.$bvModal.hide('modal-lg')
                this.scienceClear()
            }else{
                variant = 'danger'
                this.$bvToast.toast("Fan haqida hamma malumot kiritilmagan", {
                    title: `Variant ${variant || 'default'}`,
                    variant: variant,
                    solid: true,
                    autoHideDelay: 50000,
                })
            }
        },
        scienceClear(){
            this.science = ""
            this.teacher = ""
            this.ball = ""
            this.dataOn = ""
            this.dataOut = ""
        },
        editScienceVal(){
            this.scienceDemo.forEach(el => {
                if (el.id == this.editScienceInfo.id) {
                    this.science = el.science
                    this.teacher = el.teacher
                    this.ball = el.ball
                    this.dataOn = el.data.dataOn
                    this.dataOut = el.data.dataOut
                }
            });
        },
    },
    mounted() {
        if (this.modal.type == 'edit') {
            this.$store.state.todo.forEach(el => {
                if (el.id == this.modal.id) {
                    this.name = el.name,
                    this.age = el.age
                    this.address = el.address
                    this.gender = el.gender
                    this.comment = el.comment
                }
            });
        }
        // this.$store.commit('oldValScienceDemo')
    },
    computed: {
        editScienceInfo(){
            return this.$store.state.editScienceInfo
        },
        lang(){
          return this.$store.state.lang
        },
    }
}
</script>
<style lang="scss">
.addTable{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #b6b4b4a2;
    z-index: 100;
    &__card{
        background: #fff;
        border-radius: 10px;
        padding: 15px 20px;
        max-width: 900px;
        width: 100%;
        max-height: 550px;
        overflow: auto;
        height: max-content;
        display: flex;
        flex-direction: column;
        gap: 10px;
        position: relative;
        &-close{
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
            img{
                width: 30px;
            }
        }
        h5{
            color: #000;
        }
        &-info{
            display: flex;
            margin-left: auto;
            gap: 10px;
        }
    }
}
.card__item{
    display: flex;
    flex-wrap: wrap;
    background: transparent;
    gap: 20px;
    color: #000;
    .card__gender,
    .card__izoh {
        display: flex;
        flex-direction: column;
        gap: 10px;
        textarea{
            max-width: 400px;
            min-width: 400px;
            max-height: 90px;
            min-height: 90px;
            padding: 5px 8px;
            outline: none;
            border-radius: 10px;
        }
    }
}
.science{
    width: 100%;
    display: flex;
    &__create{
        margin-left: auto;
    }
    &__item{
        padding: 20px;

    }
    &__parent{
        background: #e9e7e7a8;
        border-radius: 10px;
        padding-bottom: 10px;
        padding-right: 10px;
        &-control{
            display: flex;
            gap: 10px;
            justify-content: end;
        }
    }
    & .card{
        &__item{
            display: flex;
            align-items: center;
            &-date{
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 10px;
            }
        }
    }

}
.card__gender{
    label{
        font-size: 20px;
        line-height: 1;
    }
}
</style>