import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lang: 'uz',
    id: 1,
    todo: [],
    modal: {
      bool: false,
      type: 'add'
    },
    scienceId: 1,
    scienceDemo: [],
    editScienceInfo: {
      type: 'add'
    },
    oldValScienceDemo: [],
  },
  mutations: {
    id(state){
      state.id++
      localStorage.setItem('id', JSON.stringify(state.id));
    },
    scienceId(state){
      state.scienceId++
      localStorage.setItem('scienceId', JSON.stringify(state.scienceId));
    },
    init(state){
      let todo = localStorage.getItem("todo");
      if (todo != null) {
        state.todo = JSON.parse(todo)
      }else{
        let todo = []
      }
      // science start
      let science = localStorage.getItem("science");
      if (science != null) {
        state.scienceDemo = JSON.parse(science)
      }else{
        let todo = []
      }
      // id
      let id = localStorage.getItem("id");
      if (id != null) {
        state.id = JSON.parse(id)
      }
      // scienceId
      let scienceId = localStorage.getItem("scienceId");
      if (scienceId != null) {
        state.scienceId = JSON.parse(scienceId)
      }
    },
    addTodo(state, payload){
      state.todo.push(payload)
      localStorage.setItem('todo', JSON.stringify(state.todo));
    },
    delTodo(state, id){
      state.todo = state.todo.filter(item=>item.id != id)
      state.scienceDemo = state.scienceDemo.filter(item=>item.parentId != id)
      localStorage.setItem('todo', JSON.stringify(state.todo));
    },
    editTodo(state, object){
      state.todo = state.todo.map(item=>item.id == object.id ? item = object : item)
      localStorage.setItem('todo', JSON.stringify(state.todo));
    },
    addModal(state, payload){
      state.modal = payload
    },
    // science crud
    addScienceDemo(state, obj){
      state.scienceDemo.push(obj)
      localStorage.setItem('science', JSON.stringify(state.scienceDemo));
    },
    clearScienceDemo(state,id){
      state.scienceDemo = state.scienceDemo.filter(item => !item.newCreate)
      localStorage.setItem('science', JSON.stringify(state.scienceDemo));
    },
    oldScienceDemo(state){
      state.scienceDemo.forEach(element => {
        if (element.newCreate) {
          element.newCreate = false
          element.newUpdate = false
        }
      });
      localStorage.setItem('science', JSON.stringify(state.scienceDemo));
    },
    newScienceUpdate(state, id){
      state.scienceDemo.forEach(element => {
        if (element.id == id) {
          element.newUpdate = true
        }
      });
    },
    addScienceInfo(state, obj){
      state.editScienceInfo = obj
      console.log(obj);
      localStorage.setItem('science', JSON.stringify(state.scienceDemo));
    },
    delScienceDemo(state, id){
      state.scienceDemo.forEach((element,i) => {
        if (element.id == id) {
          state.scienceDemo.splice(i,1)
        }
      });
      localStorage.setItem('science', JSON.stringify(state.scienceDemo));
    },
    newScienceDemo(state, obj){
      state.scienceDemo.forEach(el => {
        if (el.id == state.editScienceInfo.id) {
          el.teacher = obj.teacher
          el.science = obj.science
          el.ball = obj.ball
          el.data = obj.data
        }
      });
      localStorage.setItem('science', JSON.stringify(state.scienceDemo));
    },
    langMutat(state,val){
      state.lang = val
      localStorage.setItem('lang', JSON.stringify(val));
    }
  },
  actions: {

  },
  getters: {
  }
})
