<template>
    <div class="sciensce">
        <!-- <b-table-simple v-if="science.length" bordered hover class="mb-0">
            <b-thead>
                <b-tr>
                    <b-th style="width: 170px" class="pt-1.5 pb-1.5 text-center">Fan nomi</b-th>
                    <b-th style="width: 220px" class="pt-1.5 pb-1.5 text-center">O'qituvchi ismi</b-th>
                    <b-th style="width: 60px" class="pt-1.5 pb-1.5 text-center">Baho</b-th>
                    <b-th style="width: 170px" class="pt-1.5 pb-1.5 text-center">Dars vaqti</b-th>
                    <b-th style="width: 200px" class="pt-1.5 pb-1.5 text-center">Amallar</b-th>
                </b-tr>
            </b-thead>

            <b-tbody>
                <b-tr v-for="(item,index) in science" :key="index">
                    <b-td style="width: 170px" class="text-center">{{ item.science }}</b-td>
                    <b-td style="width: 220px">{{ item.teacher }}</b-td>
                    <b-td style="width: 60px" class="text-center">{{ item.ball }}</b-td>
                    <b-td style="width: 170px" class="text-center">{{ item.dataOn }} - {{ item.dataOut }}</b-td>
                    <b-td style="width: 200px" class="controller">
                        <b-button
                            @click="editScience(item.id)"
                            variant="success"
                        >
                            Edit
                        </b-button>
                        <b-button
                            variant="danger"
                            @click="delScience(item.id)"
                        >
                            Delete
                        </b-button>
                    </b-td>
                </b-tr>
            </b-tbody>

        </b-table-simple>
        
        <h4 v-else class="text-center mt-3 mb-3">Hech narsa topilmadi</h4> -->
        <b-table
            :items="science"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filter-included-fields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            stacked="md"
            show-empty
            small
            bordered
            striped
            class="mt-4"
        >
        
            <template #cell(data)="row">
                {{ row.item.data.dataOn }} : {{ row.item.data.dataOut }}
            </template>

            <template #cell(actions)="row" class="form-actions">
            <b-button size="sm" variant="success" @click="editScience(row.item.id)">
                edit
            </b-button>
            <b-button size="sm" variant="danger" @click="delScience(row.item.id)">
                delete
            </b-button>
            </template>
    
            <template #row-details="row">
            <b-card>
                <ul>
                <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
                </ul>
            </b-card>
            </template>
        </b-table>
        <b-row>
            <b-col sm="7" md="6" class="my-1">
            <b-pagination
                v-model="currentPage"
                :total-rows="totalRows"
                :per-page="perPage"
                align="fill"
                size="sm"
                class="my-0"
            ></b-pagination>
            </b-col>
        </b-row>
        </div>
</template>
<script>
export default {
props:{
    parentId:Number,
    scienceLang:Object
},
methods:{
    editScience(id){
        this.$store.commit('addScienceBool',true)
        let obj = {
            id,
            type: 'edit'
        }
        this.$store.commit('addScienceInfo', obj)
        this.$emit('editScienceVal')
        this.$store.commit('newScienceUpdate',id)
        this.$bvModal.show('modal-lg')
    },
    delScience(id){
        this.$store.commit('delScienceDemo',id)
    },
},
data() {
    return {
        scienceOne: this.$store.state.scienceDemo,
        totalRows: 1,
        currentPage: 1,
        perPage: 5,
        pageOptions: [5, 10, 15, { value: 100, text: "Show a lot" }],
        sortBy: '',
        sortDesc: false,
        sortDirection: 'asc',
        filter: null,
        filterOn: []
    }
},
computed: {
    science(){
        return this.scienceOne.filter(item=>item.parentId == this.parentId)
    },
    lang(){
        return this.$store.state.lang
    },
    // scienceName
    fields(){
        return [
            { key: 'science', label: this.scienceLang.scienceName[this.lang]},
            { key: 'teacher', label: this.scienceLang.teacherName[this.lang]},
            { key: 'ball', label: this.scienceLang.ball[this.lang], sortable: true, class: 'text-center',  variant: 'success' },
            { key: 'data', label: this.scienceLang.data[this.lang], sortable: true, variant: 'info' },
            { key: 'actions', label: this.scienceLang.actions[this.lang]}
        ]
    },
},
mounted() {
    this.totalRows = this.science.length
},
updated() {
    // Set the initial number of items
    this.totalRows = this.science.length
},
}
</script>
<style lang="scss">
.sciensce{
    border: 1px solid green;
    border-radius: 20px;
    padding: 20px 10px;
    border-radius: 10px;
}
</style>