<template>
    <div class="navbar">
        <h2 class="navbar__title">{{ navbarLang.listStudents[lang] }}</h2>
        <span class="navbar__info" @click="addModal">
            <span>{{ navbarLang.addStudents[lang] }}</span>
            <span class="navbar__plus">+</span>
        </span>
    </div>
</template>
<script>
export default {
    data() {
        return {
        }
    },
    props: {
        navbarLang:Object
    },
    computed: {
        lang(){
            return this.$store.state.lang
        }
    },
    methods: {
        addModal(){
            let obj = {
                bool: true,
                type: 'add'
            }
            this.$store.commit('addModal', obj)
        }
    },
}
</script>
